@import "../../styles/extends.scss";

.header {
  position: sticky;
  top: 0;
  padding: 16px 0;

  background-color: #ffffff;

  &__inner {
    @extend %flex-row-center;
  }

  &__delivery-info {
    @extend %flex-row-center;

    margin-left: 7vw;
  }

  &__search {
    margin-left: auto;
  }

  &__link {
    margin-left: 40px;
    color: #1f1f1f;
    padding: 13px 0;
    white-space: nowrap;

    &:hover {
      text-decoration: underline;
    }
  }
}
